import React, { Fragment, useEffect, useState } from 'react';
import './Home.scss';
import { isMobile } from 'react-device-detect';
import axios from 'axios';

const Home = props => {
  const { imageConfig, api_key } = props;
  const [popularDatas, setPopularDatas] = useState({
    results: [],
  });
  useEffect(() => {
    axios({
      method: 'get',
      baseURL: 'https://api.themoviedb.org',
      url: '/3/movie/popular',
      params: {
        api_key,
        language: 'ko',
      },
    }).then(res => {
      setPopularDatas({ ...res.data });
    });
  }, []);
  return (
    <Fragment>
      {isMobile ? (
        <div className="mob-home">
          <div className="main-backdrop">
            {popularDatas.results.length > 0 ? (
              <img
                src={`${imageConfig.base_url}${imageConfig.backdrop_sizes[3]}${popularDatas.results[0].backdrop_path}`}
              />
            ) : (
              <div>없음</div>
            )}

            <div className="info-wrap">
              <div className="title">제목</div>
              <div className="overview">내용</div>
            </div>
          </div>
          <div className="contents-wrap"></div>
        </div>
      ) : (
        <div className="pc-home">PC</div>
      )}
    </Fragment>
  );
};

export default Home;
